






























export default {
	data() {
		return {
			info:'',
			dc:'',
			dc_id:0,
			status:0
		};
	},
	onLoad(option) {
		var that = this;
		uni.showLoading({
			title:'数据加载中...'
		})
		uni.request({
			url:that.http+'index/getDcList',
			header:that.header,
			method:'POST',
			data:{
				net_id:option.id
			},
			success: (res) => {
				uni.hideLoading()
				that.info = res.data.net_info;
				that.dc = res.data.data;
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	},
	methods:{
		// 选择电池id
		changeDc(e,id){
			this.status = e.currentTarget.dataset.num
		},
		// 打电话
		call(){
			uni.makePhoneCall({
				phoneNumber:this.info.link_tel.toString()
			})
		},
		// 点击预约安装
		next(){
			var that = this;
			// 先判断是否实名
			uni.request({
				url:that.http+'index/ifIdcard',
				method:'POST',
				header:that.header,
				data:{
					user_id:uni.getStorageSync('token')
				},
				success: (res) => {
					if(res.data.msg=='已实名'){
						uni.navigateTo({
							url:'../yuYue2/yuYue2'
						})
					}else{
						uni.navigateTo({
							url:'../realName/realName'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'网络延迟，请刷新后重试!',
						icon:'none'
					})
				}
			})
		}
	}
}
